<template lang="pug">
#map.absolute.absolute--fill
</template>

<script>
import mapboxgl from 'mapbox-gl';
import MapboxGeocoder from '@mapbox/mapbox-gl-geocoder';
import U from 'mapbox-gl-utils';
const d3 = require('d3-fetch');
import { sheets2geojson } from 'sheets2geojson';
import EventBus from '../EventBus';

function toPins(rows) {
    return {
        type: 'FeatureCollection',
        features: rows
            .filter(row => row.Hide !== 'Yes' && row.Longitude && row.Latitude)
            .map((row, id) => ({
                type: 'Feature',
                geometry: {
                    type: 'Point',
                    coordinates: [+row.Longitude, +row.Latitude],
                },
                properties: {
                    id,
                    ...row,
                },
            })),
    };
}

function updateNearestLines(map, result) {
    const linesData = {
        type: 'FeatureCollection',
        features: window.Sites.nearestSites.map(site => ({
            type: 'Feature',
            properties: {},
            geometry: {
                type: 'LineString',
                coordinates: [
                    result.geometry.coordinates,
                    site.geometry.coordinates,
                ],
            },
        })),
    };
    console.log(linesData);
    map.U.setData('nearest-lines', linesData);
}

export default {
    data: () => ({
        selectedId: undefined,
    }),
    async mounted() {
        mapboxgl.accessToken =
            'pk.eyJ1Ijoic3RldmFnZSIsImEiOiJGcW03aExzIn0.QUkUmTGIO3gGt83HiRIjQw';
        const map = new mapboxgl.Map({
            container: 'map',
            center: [145.07, -37.652],
            maxBounds: [140.5, -39, 150.5, -33],
            zoom: 9.5,
            style:
                'https://api.maptiler.com/maps/bright/style.json?key=gXBOrR6nsbonH06fGES5',
            hash: window.location.hostname === 'localhost' ? 'loc' : false,
        });
        window.Map = this;
        this.map = map;
        window.map = map;

        this.initGeocoder(map);

        U.init(map, mapboxgl);
        map.U.onLoad(() => this.initMap(map));
        EventBus.$on('highlight-site', site => this.highlight(site));
    },
    computed: {
        iconSize() {
            return [
                'interpolate',
                ['linear'],
                ['zoom'],
                9,
                [
                    'case',
                    ['==', ['get', 'id'], this.selectedId || null],
                    0.25,
                    0.125,
                ],
                11,
                [
                    'case',
                    ['==', ['get', 'id'], this.selectedId || null],
                    0.5,
                    0.25,
                ],
            ];
        },
    },
    methods: {
        highlight(site) {
            if (!site) {
                return;
            }
            this.map.flyTo({ center: site.geometry.coordinates, zoom: 13 });
            window.FeatureInfo.feature = site;

            this.selectedId = site.properties.id;
            this.map.U.setIconSize(
                [
                    'sites-pins',
                    'sites-highlighted',
                    'sites-highlighted-outline',
                ],
                this.iconSize
            );
            this.map.U.setFilter(
                ['sites-highlighted', 'sites-highlighted-outline'],
                ['==', ['get', 'id'], this.selectedId]
            );
        },
        zoomToBounds(bbox) {
            this.map.fitBounds(bbox);
        },
        initGeocoder(map) {
            const geocoder = new MapboxGeocoder({
                mapboxgl,
                accessToken: mapboxgl.accessToken,
                countries: 'au',
                types:
                    'region,postcode,district,place,locality,neighborhood,address',
                bbox: [141, -39, 150.5, -33],
                filter: item =>
                    item.context.find(
                        ({ id, text }) =>
                            id.match(/^region/) && text === 'Victoria'
                    ),
            });
            document
                .getElementById('geocoder')
                .appendChild(geocoder.onAdd(map));

            geocoder
                .on('result', ({ result }) => {
                    console.log(result);
                    EventBus.$emit('geocode-result', result.geometry);
                    this.$nextTick(() => updateNearestLines(map, result));
                })
                .on('clear', () => {
                    window.Sites.home = null;
                });
        },
        async initMap(map) {
            // const d3 = require('d3-fetch');
            // const sheetID =
            //     '2PACX-1vTDD5T6yrNUGMfIiIOakohs3_PrRUrqUEQX0m5BxZ9kQ8sqhbSEwPvPL9pwh-9RDKNXSUpblBC3Yws5';
            const csvUrl =
                'https://docs.google.com/spreadsheets/d/e/2PACX-1vTDD5T6yrNUGMfIiIOakohs3_PrRUrqUEQX0m5BxZ9kQ8sqhbSEwPvPL9pwh-9RDKNXSUpblBC3Yws5/pub?output=csv';
            const points = toPins(await d3.csv(csvUrl));
            console.log(points);
            // console.log(sheets2geojson);
            // const points = await sheets2geojson(sheetID);

            EventBus.$emit('sites-loaded', points.features);
            console.log(points);
            map.U.addGeoJSON('sites', points);
            map.U.loadImage('Operating', 'markers/green-marker-tick.png');
            map.U.loadImage('Closed', 'markers/red-marker-x2.png');
            map.U.loadImage(
                'Contact Provider',
                'markers/blue-marker-question2.png'
            );
            map.U.loadImage('Outline', 'markers/outline-marker.png');
            // map.U.loadImage('other-marker', 'markers/orange-map-marker.png');
            map.U.loadImage(
                'marker-highlight',
                'markers/darkblue-map-marker.png'
            );
            map.U.addCircle('sites-circles', 'sites', {
                circleColor: [
                    'match',
                    ['get', 'Service_Status'],
                    'Operating',
                    'hsl(120,60%,30%)',
                    'Closed',
                    'hsl(0,60%,30%)',
                    'Contact Provider',
                    'hsl(240,60%,30%)',
                    'black',
                ],
                circleRadius: {
                    stops: [
                        [10, 3],
                        [12, 5],
                    ],
                },
            });

            map.U.addSymbol('sites-pins', 'sites', {
                iconImage: ['get', 'Service_Status'],
                iconSize: this.iconSize,
                iconOffset: [0, -60],
                textField: ['step', ['zoom'], '', 13, ['get', 'Service_Name']],
                textOffset: [0, 1],
                textOptional: true,
                textSize: 14,
                textHaloColor: 'hsla(60,20%,90%,0.8)',
                textHaloWidth: 2,
            });
            map.U.addSymbol('sites-highlighted', 'sites', {
                iconImage: ['get', 'Service_Status'],
                iconSize: this.iconSize,
                iconOffset: [0, -60],
                iconAllowOverlap: true,
                filter: false,
            });
            map.U.addSymbol('sites-highlighted-outline', 'sites', {
                iconImage: 'Outline',
                iconSize: this.iconSize,
                iconOffset: [0, -60],
                filter: false,
                iconAllowOverlap: true,
                iconIgnorePlacement: true,
            });
            map.U.addGeoJSON('nearest-lines');
            map.U.addLine('nearest-lines', 'nearest-lines', {
                lineColor: 'hsla(220,50%,50%,0.8)',
                lineDasharray: [2, 2],
                // lineOpacity: 0.6,
            });
            map.U.hoverPopup(
                ['sites-circles', 'sites-pins'],
                f => f.properties.Service_Name
            );
            map.U.hoverPointer(['sites-pins', 'sites-circles']);
            map.U.clickLayer(
                ['sites-pins', 'sites-circles'],
                ({ features: [site] }) => {
                    // console.log(e);
                    window.FeatureInfo.feature = site;
                    window.Map.highlight(site);
                    window.App.sidebarOpen = true;
                    document.getElementById('FeatureInfo').scrollIntoView();
                }
            );
            map.on('mousedown', () => {
                if (document.getElementById('map').clientWidth < 150) {
                    window.App.sidebarOpen = false;
                }
            });
        },
    },
};
import 'mapbox-gl/dist/mapbox-gl.css';
import '@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css';
</script>

<style>
.mapboxgl-ctrl-geocoder {
    /* min-width: 270px !important; */
    width: 100% !important;
    box-shadow: none !important;
    border: 1px solid lightgrey;
}
</style>
